.about__title {
  @include spacing(margin, $top: 0.5em, $bottom: 0.5em);
  @include text($align: center);
}

.about__text {
  @include spacing(margin, $bottom: 0.75em);
}

.about__image {
  height: 1rem;
}

.about__content {
  @include breakpoint-up(small) {
    display: grid;
    /* grid-template-columns: 1fr; */
    /* grid-template-rows: repeat(2, 1fr); */
    place-items: center;
  }

  /* @include breakpoint-up(large) {
    justify-content: center;
  } */
}

.about__paragraphs {
  max-width: 60ch;

  /* @include breakpoint-up(small) {
    @include spacing(margin, $right: 2em);
  } */
}

.about__links {
  @include breakpoint-up(small) {
    width: 60ch;
    align-self: flex-start;
  }
}

.about__link a {
  position: relative;
  white-space: nowrap; //because otherwise the after looks terrible

  /* &::after {
    @include link-after(-10%, 4px, 2px, 105%, $primary-colour);
    transform: skew(-24deg) rotate(-0.5deg) scale(0);
  }

  &:hover::after,
  &:focus::after {
    transform: skew(-24deg) rotate(-0.5deg) scale(1);
  } */
}
